<template>
    <div>
        <Headers title="" detail="" />
        <div class="userinfo">
            <p>
              <label></label>
              <input type="text" placeholder=""/>
            </p>
            <p>
              <label></label>
              <input type="text" placeholder=""/>
            </p>
        </div>
        <p class="charge_btn"><button></button></p>
        <p class="wenxintips">
        <br />
        1.
        <br />
        2.<br />
        </p>
        
    </div>
</template>
<script>
import Headers from '../common/NewHead'
export default {
    components:{
        Headers
    }
}
</script>
<style>
.userinfo{
    margin-top:80px;
    padding:0 0.4rem;
}
.userinfo p{
  height:1rem;
  line-height:1rem;
  color:#333;
  font-size:0.42rem;
  margin-bottom:0.4rem;
  font-family: SimHei;
}
.userinfo input {
    height:0.8rem;
    border-radius:5px;
    border:1px solid #ccc;
    margin-left:0.2rem;
    width:6rem;
    padding-left:0.2rem;
}
.charge_btn{
    margin-top:0.4rem;
    height:1rem;
    line-height:1rem;
    text-align:center;
}
.charge_btn button{
    width:7rem;
   background-image: linear-gradient(0deg, 
		#d20000 0%, 
		#ff2c2c 100%);
    color:#fff;
    font-size:0.4rem;
    border:none;
    height:1rem;
    border-radius:5px;
}
.wenxintips{
    margin:0.2rem;
    color:#999;
    font-size:0.35rem;
}
</style>




